<template>
    <div>
        <head-top webActive="mine"></head-top>
        <div class="bgGray" id="unIndex">
            <div class="container">
                <div class="ucBg">
                    <side-bar></side-bar>
                    <div class="ucPanel ucP_Box">
                        <el-tabs v-model="activeName" @tab-click="handleClick" >
                            <el-tab-pane v-for="(item,index) in type" :key="index" :label="item.value" :name="item.value"></el-tab-pane>
                        </el-tabs>
                        <div class="o_outBox">
                            <div class="o_d_top">
                                <div class="o_d_pro flex f-nowrap">
                                    <div class="o_d_date">

<!--                                        <el-dropdown>-->
<!--                                            <span class="el-dropdown-link">-->
<!--                                              近三个月订单<i class="el-icon-arrow-down el-icon&#45;&#45;right"></i>-->
<!--                                            </span>-->
<!--                                            <el-dropdown-menu slot="dropdown">-->
<!--                                                <el-dropdown-item>近三个月订单</el-dropdown-item>-->
<!--                                                <el-dropdown-item>2019年订单</el-dropdown-item>-->
<!--                                            </el-dropdown-menu>-->
<!--                                        </el-dropdown>-->
                                    </div>
                                    <div class="o_d_detail">
                                        订单详情
                                    </div>
                                </div>
                                <div class="o_d_receiver">
                                    收货人
                                </div>
                                <div class="o_d_price">
                                    金额
                                </div>
                                <div class="o_d_state">
                                    <el-dropdown @command="handleCommand">
                                            <span class="el-dropdown-link">
                                              {{activeName}}<i class="el-icon-arrow-down el-icon--right"></i>
                                            </span>
                                        <el-dropdown-menu slot="dropdown">
                                            <el-dropdown-item v-for="(item,index) in type" :key="index" :command="item.name">{{item.value}}</el-dropdown-item>
                                        </el-dropdown-menu>
                                    </el-dropdown>
                                </div>
                                <div class="o_d_control">
                                    操作
                                </div>
                            </div>
                            <div v-if="!login" class="tc mt30">
                                <el-button type="warning" plain @click="toLogin">
                                    请先登录
                                </el-button>
                            </div>
                            <div v-else-if="tableData.length==0" class="tc f16 mt30">暂无数据</div>
                            <div class="i_order" v-else v-for="(item,index) in tableData" :key="index">
                                <div class="i_order_top">
                                    <span>订单号：{{item.order_code}}</span>
                                    <span>{{item.time}}</span>
                                </div>
                                <div class="i_o_content flex f-nowrap">
                                    <div class="o_d_pro">
                                        <div class="i_o_item flex f-nowrap" v-for="(iitem,iindex) in item.goods" :key="iindex">
                                            <el-image class="i_o_pic" fit="contain" :src="iitem.pic"  @click="toGood(iitem.specifications_id)"></el-image>
                                            <div class="i_o_info">
                                                <p class="color3 i_o_name"  @click="toGood(iitem.specifications_id)">{{iitem.name}}</p>
                                                <p class="color9">规格：{{iitem.spec_name}}{{iitem.spec_unit}}</p>
                                                <p class="color6 flex f-nowrap w j-bet">
                                                    <span>￥{{iitem.price}}/{{iitem.supply_unit}}</span>
                                                    <span class="ml10">×{{iitem.number}}</span>
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="o_d_receiver o_bLf">
                                        <div v-if="item.username==''">自提</div>
                                        <el-popover
                                                v-else
                                                placement="top"
                                                width="200"
                                                trigger="hover">
                                            <div>
                                                <p class="f12 mb5">{{item.username}}</p>
                                                <p class="f12 mb5">{{item.address}}</p>
                                                <p class="f12">{{item.mobile}}</p>
                                            </div>
                                            <span slot="reference"><i class="el-icon-user color9 mr5"></i>{{item.username}}</span>
                                        </el-popover>
                                    </div>
                                    <div class="o_d_price o_bLf">
                                        总额：￥{{item.price_total}}
                                        <p class="color9" v-if="item.is_pay!=0">{{item.pay_type==0?'线下支付':'线上支付'}}</p>
                                        <p class="color9" v-else>未支付</p>
                                    </div>
                                    <div class="o_d_state o_bLf">
                                        <p :class="item.state=='待付款'?'colorPrice':item.state=='取消订单'?'color9':'colorGreen'">{{item.state}}</p>
                                        <p><span class="cro_btn" @click="toDetail(item.order_id)">订单详情</span></p>
                                    </div>
                                    <div class="o_d_control  o_bLf">
                                        <p><el-button size="mini" v-if="item.state=='待付款'" @click="payNow(item.order_id)" type="warning" plain>立即付款</el-button></p>
                                        <p><el-button size="mini" type="primary" @click="orderConfirm(item.order_id)" v-if="item.state=='已付款'||item.state=='配送中'" plain>确认收货</el-button></p>
                                        <p><span class="cro_btn" @click="cancelOrder(item.order_id)" v-if="item.state=='待付款'">取消订单</span></p>
                                        <p><el-button size="mini" type="success" class="cro_btn cro_eva" plain @click="toEvaluate(item.order_id)" v-if="item.state=='已签收'&&item.is_comment==0">评价</el-button></p>
                                        <p><el-button type="text" v-if="item.state=='已签收'&&item.is_comment==1" disabled>已评价</el-button></p>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <el-pagination
                                class="tc mt15"
                                @current-change="handleCurrentChange"
                                hide-on-single-page
                                background
                                layout="prev, pager, next"
                                :current-page="page"
                                :page-size="rows"
                                :total="count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import {mapState, mapMutations, mapActions} from 'vuex'
    import sideBar from '../../../components/usercenter/sideBar';
    import headTop from '../../../components/header/headTop';
    import {getMyOrderList,orderCancel,orderConfirm} from '../../../service/api'
    export default {
        name: 'order',
        data(){
            return{
                input:'',
                activeName: '全部',
                tableData: [],
                rows:4,//每页显示数
                page:1,//当前页面
                count:0,//总条数
                type:[
                    {
                        name:'all',
                        value:'全部'
                    },
                    {
                        name:'weifukuan',
                        value:'待付款'
                    },
                    {
                        name:'yifukuan',
                        value:'待收货'
                    },
                    {
                        name:'daipinglun',
                        value:'待评价'
                    },
                    {
                        name:'quxiao',
                        value:'已取消'
                    },

                ]
            }
        },
        components:{
            sideBar,
            headTop
        },
        mounted(){
            this.getUserInfo();
            this.initData();
        },
        computed:{
            ...mapState([
                'userInfo','login'
            ]),
        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),
            handleClick(tab, event) {
                this.page = 1;
                console.log("target:"+event.target.getAttribute('id').substr(4));
                var val = event.target.getAttribute('id').substr(4);
                this.activeName = val;
                var nameTarget;
                for(var i in this.type){
                    if(this.type[i].value == val){
                        nameTarget = this.type[i].name;
                    }
                }

                this.changeType(nameTarget);
            },
            handleCommand(command) {
                this.page = 1;
                console.log("command:"+command);
                for(var i in this.type){
                    if(this.type[i].name == command){
                        this.activeName = this.type[i].value;
                    }
                }
                this.changeType(command);
            },
            async initData(){
                if(this.$route.query.activeName){
                    this.changeType(this.$route.query.activeName);
                }else{
                    this.getList('');
                }
            },
            async getList(activeTar){
                //yifukuan = yifukuan + peisong
                if(activeTar == 'yifukuan'){
                    var res1 = await getMyOrderList(
                        'yifukuan',//type
                        this.rows,
                        this.page
                    );
                    var res2 = await getMyOrderList(
                        'peisong',//type
                        this.rows,
                        this.page
                    );
                    this.tableData=res1.data;
                    for(var i = 0 ; i < res2.data.length ; i++){
                        this.tableData.push(res2.data[i]);
                    }
                    this.count = res1.count + res2.count;
                    this.activeName = '待收货';
                }else{
                    var res = await getMyOrderList(
                        activeTar,
                        this.rows,
                        this.page
                    );

                    if(activeTar == ''){
                        this.activeName = '全部';
                    }else{
                        for(var i in this.type){
                            if(this.type[i].name == activeTar){
                                this.activeName = this.type[i].value;
                            }
                        }
                    }
                    if(res.status!=12007){
                        this.count = res.count;
                        this.tableData = res.data;
                    }else{
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                    }

                }
                console.log("this.count:"+this.count);
                console.log("activeName:"+this.activeName);
            },
            toEvaluate(order_id){
                this.$router.push({path:"/userCenter/evaluate/evaluate",query:{order_id:order_id}});
            },
            toDetail(order_id){
                this.$router.push({path:"/userCenter/order/detail",query:{order_id:order_id}});
            },
            toGood(spec_id){
                this.$router.push({path:"/good/detail",query:{specifications_id:spec_id}});
            },
            payNow(orderId){
                this.$router.push({path:"/order/pay",query:{orderId:orderId}});
            },
            orderConfirm(order_id){
                this.$confirm('是否确认收货?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    let res = await orderConfirm(order_id);
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.initData();
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {
                });
            },
            toLogin(){
                this.$router.push({path:"/login/login"});
            },
            async changeType(tar){
                //this.activeName = tar=='all'?'':tar;
                //tar = tar=='all'?'':tar;
                if(tar=='yifukuan'){
                    this.getList('yifukuan');
                }else if(tar=='all'){
                    this.getList('');
                }else{
                    this.getList(tar);
                }
            },
            cancelOrder(order_id){
                console.log(order_id);
                this.$confirm('你确定要取消该订单吗?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    let res = await orderCancel(order_id);
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.changeType(this.activeName);
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {

                });

            },
            handleCurrentChange(val) {
                //改变页码
                this.page = val;
                var tar = '';

                for(var i in this.type){
                    if(this.type[i].value == this.activeName){
                        tar = this.type[i].name=='all'?'':this.type[i].name;
                    }
                }

                this.getList(tar);
            }


        },
        created(){

        },
        watch:{
            userInfo: function (value) {
                if (value && value.user_id && !this.list) {
                    this.initData();
                }
            }

        }

    }

</script>

<style scoped>
    /*我的订单*/
    .o_d_top{
        display: flex;
        flex-wrap: nowrap;
        background-color: #f5f5f5;
    }
    .o_d_top .o_d_pro,.o_d_top .o_d_receiver,.o_d_top .o_d_price,.o_d_top .o_d_state,.o_d_top .o_d_control{
        padding:5px 10px;
    }
    .o_d_pro{
        width:50%;
    }
    .o_d_date{
        width:200px;
    }
    .o_d_receiver{
        width:12%;
        text-align: center;
        font-size: 13px;
    }
    .o_d_receiver:hover{
        cursor: default;
    }
    .o_bLf{
        border-left: 1px solid #e4e4e4;
        padding:10px;
    }
    .o_d_price{
        width:13%;
        text-align: center;
        font-size: 13px;
    }
    .o_d_price p{
        border-top:1px solid #e4e4e4;
    }
    .o_d_state{
        width:13%;
        text-align: center;
        font-size: 13px;
    }
    .o_d_control{
        width:12%;
        text-align: center;
        font-size: 13px;
    }
    .i_order_top{
        background-color: #f5f5f5;
        margin-top:15px;
        font-size: 12px;
        line-height: 30px;
        padding:0 15px;
        border:1px solid #e4e4e4;
        border-bottom: none;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    .i_o_content{
        border:1px solid #e4e4e4;
        border-top:none;
    }
    .i_o_item{
        padding:10px;
    }
    .i_o_pic{
        width:80px;
        height: 80px;
        flex-shrink: 0;
        border:1px solid #e4e4e4;
    }
    .i_o_info{
        padding:0 0 0 15px;
        flex-shrink: 1;
        font-size: 13px;
        width: 100%;
    }
    .i_o_name{
        line-height:18px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }
    .i_o_name:hover{
        color:#0bb8b2;
        cursor: pointer;
    }
    .cro_btn{
        margin:5px 0;
        display: inline-block;
    }
    .cro_btn:hover{
        color:#0bb8b2;
        cursor: pointer;
        border-bottom:1px solid #0bb8b2;
    }
    .cro_eva:hover{
        color:#fff;
    }



</style>



